
<template>
  <div>
      <div @click="changeOn" :class="isOff?'isOff':'isOn'">
 
      </div>
      <audio id="audio" src="http://www.170hi.com/kw/other.web.rf01.sycdn.kuwo.cn/resource/n2/80/69/1039022526.mp3"></audio>
  </div>
</template>
<script>

export default {
    data() {
      return {
          isOff: true
      }
    },
    components: {},
    created(){
        
    },
    mounted() {
        // Ëá™Âä®Êí≠ÊîæÈü≥‰πêÊïàÊûúÔºåËß£ÂÜ≥ÂæÆ‰ø°Ëá™Âä®Êí≠ÊîæÈóÆÈ¢ò
       document.addEventListener('touchstart',this.audioAutoPlay,false);
       document.addEventListener('WeixinJSBridgeReady', this.audioAutoPlay,false);
       let oAudio = document.querySelector("#audio");
        oAudio.onended = function () {//Êí≠ÊîæÂÆåÊØïÔºåÈáçÊñ∞Âæ™ÁéØÊí≠Êîæ
        oAudio.load();
            oAudio.play();
        }

        
    },
    methods: { 
           changeOn(){
                let oAudio = document.querySelector("#audio");
               if(this.isOff){
                oAudio.play();//ËÆ©Èü≥È¢ëÊñá‰ª∂ÂºÄÂßãÊí≠Êîæ     
               }else{
                oAudio.pause();//ËÆ©Èü≥È¢ëÊñá‰ª∂ÊöÇÂÅúÊí≠Êîæ 
               }
               this.isOff = !this.isOff;
           },
           audioAutoPlay() {
                let audio = document.getElementById('audio');
                    this.isOff = false;
                    audio.play();
                document.removeEventListener('touchstart',this.audioAutoPlay);
            }
           
    }
  }
</script>
 
<style lang="less" scoped>
.isOn{
    width: 28px;
    height: 28px;
    position: fixed;
    z-index: 2000;
    top: 20px;
    right: 20px;
     -webkit-animation: rotating 1.2s linear infinite;
    animation: rotating 1.2s linear infinite;
    background: url("../images/music.png") 0 0 no-repeat;
    background-size:100%; 
}
@keyframes rotating {
    from { -webkit-transform: rotate(0) }
    to { -webkit-transform: rotate(360deg) }
 }
@-webkit-keyframes rotating {
    from { -webkit-transform: rotate(0) }
    to { -webkit-transform: rotate(360deg) }
 }
.isOff{
    width: 28px;
    height: 28px;
    position: fixed;
    z-index: 2000;
    top: 20px;
    right: 20px;
    background: url("../images/music.png") 0 0 no-repeat;
    background-size:100%; 
}
</style>